import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Main from '../layouts/Main';

import Education from '../components/Resume/Education';
import Experience from '../components/Resume/Experience';

import skills from '../data/resume/skills';
import degrees from '../data/resume/degrees';
import work from '../data/resume/work';

const Resume = () => (
  <Main
    title="Resume"
    description="Kshitij Bhausaheb Pawar's Resume. Cloud Security Engineer skilled in Terraform, Azure, AWS, Microsoft Defender, and SOC operations."
  >
    <article className="post" id="resume">
      <header>
        <div className="title">
          <h2>
            <Link to="/resume">Resume</Link>
          </h2>
          <div className="link-container">
            <h4><a href="#education">Education</a></h4>
            <h4><a href="#experience">Experience</a></h4>
            <h4><a href="#skills">Skills</a></h4>
          </div>
        </div>
      </header>

      <Education data={degrees} id="education" />
      <Experience data={work} id="experience" />
      <section id="skills" style={{ maxWidth: '1100px', margin: '0 auto', padding: '0 1rem' }}>
        <h3 style={{ textAlign: 'center' }}>Skills</h3>
        <div
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            gap: '1rem',
          }}
        >
          {skills.map(({ category, items }) => {
            const [showAll, setShowAll] = useState(false);
            const visibleItems = showAll ? items : items.slice(0, 5);
            return (
              <div key={category} style={{ width: '280px', marginBottom: '1rem' }}>
                <strong>{category}</strong>
                <ul style={{ paddingLeft: '1rem' }}>
                  {visibleItems.map((skill) => (
                    <li key={skill}>{skill}</li>
                  ))}
                </ul>
                {items.length > 5 && (
                  <button type="button" onClick={() => setShowAll(!showAll)}>
                    {showAll ? 'Show Less' : 'More'}
                  </button>
                )}
              </div>
            );
          })}
        </div>
      </section>
    </article>
  </Main>
);

export default Resume;
